<template>
  <div class="startUp">
    <video
      poster="../assets/Snipaste_2024-06-19_09-16-58.png"
      ref="video"
      src="../assets/a90688160325a384a3baf31672313798.mp4"
    ></video>

    <div class="skip" @click="skip">{{ $t("index.text177") }}</div>
  </div>
</template>

<script>
export default {
  name: "startUpVue",

  mounted() {
    // this.$router.push("/index");
    // return;
    this.$refs.video.play();
    this.$refs.video.addEventListener("ended", () => {
      this.$router.push("/index");
    });
  },
  methods: {
    skip() {
      this.$refs.video.pause();
      this.$router.push("/index");
    }
  }
};
</script>

<style lang="less" scoped>
.startUp {
  height: 100vh;
  position: relative;
}
.skip {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  z-index: 999;
  color: #fff;
  padding: 1rem 2rem;
  border-radius: 2rem;
  background: rgba(255, 255, 255, 0.4);
}
img {
  width: 100vw;
  height: 100vh;
}
video {
  width: 100vw;
  height: 100%;
  object-fit: cover; /* 这个属性可以确保视频铺满并保持比例 */
}
</style>



